const initialData = [];

const reducer = (state = initialData, action) => {
  switch (action.type) {
    case "ADD_PRODUCT":
      state = [...state, action.payload]
      return state;
    case "UPDATE_PRODUCT":
      const contactUpdate = state.filter((contact) =>
        contact.id === action.payload.id
          ? Object.assign(contact, action.payload)
          : contact
      );
      state = contactUpdate;
      return state;
    default:
      return state;
  }
}

export default reducer;