import React, { useState } from "react";
import { useDispatch, useSelector } from 'react-redux';
import { Form, Button, Card, Alert, Container } from "react-bootstrap";
import { useHistory } from "react-router-dom";

export default function AddProduct() {

  const products = useSelector((state) => state);
  const dispatch = useDispatch();

  const history = useHistory();

  const [data, setState] = useState({
    name: '',
    description: '',
    price: '',
    quantity: '',
    image: ''
  });

  const [error, setError] = useState("");

  const { name, description, price, quantity, image } = data;

  const handleInputChange = (e) => {
    let { name, value } = e.target;
    setState({ ...data, [name]: value })
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!name || !price || !quantity) {
      setError("Please fill the required Fields")
    }

    const data = {
      id: products.length > 0 ? products[products.length - 1].id + 1 : 1,
      name,
      description,
      price,
      quantity,
      image
    };

    dispatch({ type: "ADD_PRODUCT", payload: data });
    history.push('/');
  }

  return (
    <Container className="p-5">
      <Card>
        <Card.Body>
          <h2 className="text-center mb-4">Add Product</h2>
          {error && <Alert variant="danger">{error}</Alert>}
          <Form
            onSubmit={handleSubmit}
          >
            <Form.Group id="name">
              <Form.Label>Name</Form.Label>
              <Form.Control type="text" value={name} name="name" required onChange={handleInputChange} />
            </Form.Group>
            <Form.Group id="description">
              <Form.Label>Description</Form.Label>
              <Form.Control type="text" value={description} name="description" onChange={handleInputChange} />
            </Form.Group>
            <Form.Group id="price">
              <Form.Label>Price</Form.Label>
              <Form.Control type="number" value={price} name="price" required onChange={handleInputChange} />
            </Form.Group>
            <Form.Group id="quantity">
              <Form.Label>Quantity</Form.Label>
              <Form.Control type="number" value={quantity} name="quantity" required onChange={handleInputChange} />
            </Form.Group>
            <Form.Group id="image">
              <Form.Label>Image</Form.Label>
              <Form.Control name="image" type="text" value={image} onChange={handleInputChange} />
            </Form.Group>
            <Button className="w-100 mt-2" type="submit">
              Add
            </Button>
          </Form>
        </Card.Body>
      </Card>
    </Container>
  )
}
